.header__top {
    position: fixed;
    top: 0;
    z-index: 101;
    height: 100vh;
    left: -40px;
    width: 280px;
    transform: translateX(-100%);
    display: none;
    flex-direction: column;
    background-color: #fff;
    user-select: none;
    will-change: transform;
    transition: transform .15s ease-in-out;

    @media (min-width: 361px) {
        width: 300px;
    }

    @media (min-width: 577px) {
        width: 372px;
        padding-bottom: unset;
    }

    @media (min-width: 1024px) {
        display: none;
    }


    .modal__close {
        position: absolute;
        top: 17px;
        right: -17px;
        width: 24px;
        height: 24px;
        display: flex;
        align-items: center;
        justify-content: center;
        transform: translateX(100%);
        z-index: 1;
        padding: 0;
        border: 0;
        background: transparent;

        @media (any-hover: hover) {
            &:hover {
                .modal__close-icon svg path {
                    fill: rgba(255 255 255 / 70%);
                }
            }
        }

        @media (min-width: 577px) {
            width: 32px;
            height: 32px;
        }
    }

    .modal__close-icon {
        width: 24px;
        height: 24px;
        display: flex;

        svg path {
            fill: rgba(255, 255, 255, 0.6);
            transition: fill 0.15s ease-in-out;
        }

        @media (min-width: 577px) {
            width: 32px;
            height: 32px;
        }
    }

    .burger-menu_contacts_btn {
        @media (max-width: 360px) {
            font-size: 13px;
        }
    }

    .burger-menu_nav_item {
        width: 100%;
    }

    .burger-menu_nav_sub-list_container {
        .burger-menu_nav_item {
           width: 90%;
           margin-left: auto;
        }
    }
}

.header__top-content {
    // padding-top: 35px;
    padding-bottom: 100px;
    height: 100vh;
    overflow: hidden;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
}

.header__top-block {
    width: 100%;
    padding: 24px;

    &:not(:first-of-type) {
        border-top: 1px solid red;
    }

    @media (min-width: 577px) {
        padding: 24px 40px;
    }
}

.header__top-main {
    position: relative;
    padding-bottom: 12px;
    display: flex;
    flex-direction: column;
    align-items: center;

    @media (min-width: 577px) {
        padding-bottom: 12px;
    }

    @media (min-width: 1201px) {
        padding: 0;
        flex-direction: row;
        justify-content: space-between;
        border: 0 !important;
    }
}

.header__top--opened {
    left: 0;
}

.header__top-backdrop {
    position: fixed;
    opacity: 0;
    pointer-events: none;
    z-index: 100;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 16, 32, 0.6);
    transition: opacity .15s ease-in-out;

    @media (min-width: 1201px) {
        display: none !important;
    }
}
