@use '../mixins/responsive';

.plan-block {

    @media (min-width: 577px) {
        padding: 20px 0;
    }

  .plan-block_form_submit {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &_container {
    border-radius: 40px;
    background: var(--green-100, #28906F);
    overflow: hidden;
    padding: 60px 36px 30px;
    position: relative;
    @include responsive.media-breakpoint-down(xl) {
      padding: 48px 36px 32px;
    }
    @include responsive.media-breakpoint-down(lg) {
      border-radius: 0;
      padding: 40px 32px 40px;
    }
    @include responsive.media-breakpoint-down(smd) {
      padding: 32px 16px 32px;

    }
    @include responsive.media-breakpoint-down(sm) {
      height: 1325px;
      display: flex;
      flex-direction: column;
    }
  }

  &_mobile-bg {
    display: none;
    @include responsive.media-breakpoint-down(sm) {
      display: block;
      position: absolute;
      bottom: 589px;
      right: 0;
      z-index: 1;
    }
  }

  &_bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    //height: 100%;
    z-index: 0;
    @include responsive.media-breakpoint-down(sm) {
      right: -70px;
      left: auto;
      top: -20px;
    }

    &.desc {
      @include responsive.media-breakpoint-down(sm) {
        display: none;
      }
    }

    &.mobile {
      display: none;
      @include responsive.media-breakpoint-down(sm) {
        display: block;
      }
    }
  }

  &_top {
    margin: 0 auto;
    max-width: 934px;
    z-index: 1;
    position: relative;
    margin-bottom: 48px;
    @include responsive.media-breakpoint-down(xl) {
      margin-bottom: 40px;
    }
    @include responsive.media-breakpoint-down(elg) {
      margin-bottom: 32px;
    }
    @include responsive.media-breakpoint-down(sm) {
      margin-bottom: auto;
    }
  }

  &_subtitle {
    color: var(--white-50) !important;
    text-align: center;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%; /* 22.4px */
    margin-bottom: 20px;
    @include responsive.media-breakpoint-down(xl) {
      margin-bottom: 16px;
    }
    @include responsive.media-breakpoint-down(elg) {
      margin-bottom: 12px;
    }
  }

  &_title {
    color: var(--white-50) !important;
    font-size: 56px;
    font-style: normal;
    font-weight: 600;
    line-height: 100%;
    text-align: center;
    margin-bottom: 32px;
    @include responsive.media-breakpoint-down(xl) {
      margin-bottom: 32px;
    }
    @include responsive.media-breakpoint-down(elg) {
      margin-bottom: 24px;
    }
    @include responsive.media-breakpoint-down(smd) {
      margin-bottom: 16px;
    }

    span {
      color: var(--white-100) !important;
    }
  }

  &_desc {
    color: var(--white-100);
    text-align: center;
    font-feature-settings: 'ss12' on;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%; /* 28px */
    max-width: 660px;
    margin: 0 auto;
    @include responsive.media-breakpoint-down(xl) {
      font-size: 16px;
      max-width: 560px;
    }
  }

  &_steps {
    position: relative;
    z-index: 1;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 12px;
    @include responsive.media-breakpoint-down(xl) {
      grid-template-columns: 1fr 1fr;
      gap: 10px;
    }
    @include responsive.media-breakpoint-down(sm) {
      display: flex;
      gap: 0;
      height: max-content;
    }

    .plan-block_step__last {
      display: block;
      @include responsive.media-breakpoint-down(sm) {
        display: none;
      }
    }
  }

  &_step {
    border-radius: 24px;
    background: var(--white-100);
    height: 420px;
    position: relative;
    overflow: hidden;
    @include responsive.media-breakpoint-down(xl) {
      height: 360px;
    }
    @include responsive.media-breakpoint-down(sm) {
      padding: 12px;
      height: 221px;
      display: flex;
      flex-direction: column;
      width: 148px;
    }

    &:nth-last-child(-n + 2) {
      @include responsive.media-breakpoint-down(lg) {
        height: 456px;
      }
      @include responsive.media-breakpoint-down(sm) {
        height: auto;
      }
    }

    &.green {
      --plan-step-number-bg: var(--green-100);
      --plan-step-number-text-bg: var(--green-10);
      --plan-step-number-text-color: var(--green-100)
    }

    &_bg {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: flex;
      align-items: flex-end;
      justify-content: flex-end;
      z-index: 0;
      @include responsive.media-breakpoint-down(sm) {
        position: relative;
        height: auto;
        top: auto;
        left: auto;
        justify-content: flex-start;
        margin-bottom: 16px;
      }

      picture {
        height: 100%;
      }

      img {
        height: 100%;
        object-fit: cover;

        @include responsive.media-breakpoint-down(sm) {
          display: none;
        }
      }
    }

    &_num {
      position: absolute;
      top: 32px;
      left: 32px;
      display: flex;
      z-index: 1;
      @include responsive.media-breakpoint-down(xl) {
        top: 24px;
        left: 24px;
      }
      @include responsive.media-breakpoint-down(sm) {
        position: relative;
        height: auto;
        top: auto;
        left: auto;
      }

      &__number {
        z-index: 2;
        display: flex;
        justify-content: center;
        align-items: center;
        min-width: 48px;
        min-height: 48px;
        border-radius: 1000px;
        border: 2px solid var(--white-100);
        background: var(--plan-step-number-bg, var(--blue-100));

        color: var(--number-color, var(--white-100));
        text-align: center;
        font-size: 16px;
        font-style: normal;
        font-weight: 700;
        line-height: 120%;
        @include responsive.media-breakpoint-down(elg) {
          min-width: 42px;
          min-height: 42px;
        }
        @include responsive.media-breakpoint-down(sm) {
          min-width: 32px;
          min-height: 32px;
          font-size: 14px;
        }
      }

      &__text {
        z-index: 1;
        border-radius: 1000px;
        background: var(--plan-step-number-text-bg, var(--blue-20));
        height: 48px;

        color: var(--plan-step-number-text-color, var(--blue-100));
        text-align: center;
        font-size: 14px;
        font-style: normal;
        font-weight: 600;
        line-height: 20px;
        margin-left: -12px;
        padding: 13px 20px 0;
        @include responsive.media-breakpoint-down(elg) {
          padding: 9px 16px 0;
          margin-left: -8px;
          height: 40px;
        }
        @include responsive.media-breakpoint-down(sm) {
          height: 32px;
          padding: 8px 16px 0;
          font-size: 12px;
          line-height: 120%;
        }
      }
    }

    &_more {
      z-index: 3;
      position: absolute;
      top: 32px;
      right: 34px;
      padding: 12px;
      border-radius: 1000px;
      background-color: var(--Card-Background);
      display: flex;
      transition: background-color 0.1s ease-in-out;

      @include responsive.media-breakpoint-down(xl) {
        top: 24px;
        right: 24px;
      }
      @include responsive.media-breakpoint-down(sm) {
        position: relative;
        top: auto;
        right: auto;
        align-items: center;
        gap: 4px;
        padding: 9px 20px;
        margin-top: auto;
        width: max-content;
      }

      @media (any-hover: hover) {
        &:hover {
            background-color: var(--grey-10);
        }
      }

      svg {
        @include responsive.media-breakpoint-down(sm) {
          width: 16px;
          height: 16px;
        }
      }

      &__text {
        display: none;
        color: var(--black-100);
        text-align: center;
        font-size: 12px;
        font-style: normal;
        font-weight: 600;
        line-height: 120%; /* 14.4px */
        @include responsive.media-breakpoint-down(sm) {
          display: block;
        }
      }
    }

    &_content {
      padding: 32px;
      position: relative;
      z-index: 2;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      height: 100%;
      @include responsive.media-breakpoint-down(xl) {
        padding: 24px;
      }
      @include responsive.media-breakpoint-down(sm) {
        padding: 0;
        height: auto;
        flex-direction: column-reverse;
      }
    }

    &_time {
      color: var(--grey-100);
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: 140%;
      margin-bottom: 16px;
      display: flex;
      align-items: center;
      gap: 4px;
      @include responsive.media-breakpoint-down(xl) {
        margin-bottom: 12px;
      }
      @include responsive.media-breakpoint-down(elg) {
        margin-bottom: 8px;
        font-size: 14px;
      }
      @include responsive.media-breakpoint-down(sm) {
        font-size: 12px;
        margin-bottom: 0;
      }

      span {
        color: var(--white-100);
        @include responsive.media-breakpoint-down(smd) {
          display: none;
        }
      }

      &:before {
        width: 20px;
        height: 20px;
        @include responsive.media-breakpoint-down(elg) {
          width: 16px;
          height: 16px;
        }
        @include responsive.media-breakpoint-down(sm) {
          content: unset;
        }
      }
    }

    &_title {
      color: var(--black-100);
      font-size: 24px;
      font-style: normal;
      font-weight: 600;
      line-height: 120%; /* 28.8px */
      @include responsive.media-breakpoint-down(elg) {
        font-size: 20px;
      }
      @include responsive.media-breakpoint-down(sm) {
        font-size: 14px;
        margin-bottom: 4px;
      }
    }

    &__last {
      display: none;
      position: relative;
      border-radius: 24px;
      border: 3px solid #6CC7AA;
      overflow: hidden;
      @include responsive.media-breakpoint-down(sm) {
        padding: 0;
        height: 494px;
        display: flex;
        flex-direction: column;
        width: 100vw;
        z-index: 10;
        border: none;
        margin-top: 24px;
        border-radius: 0;
        margin-left: -16px;
      }

      img {
        z-index: 3;
        @include responsive.media-breakpoint-down(sm) {
          display: block;
          height: auto;
          max-width: 359px;
        }
      }

      .plan-block_step_content {
        background: linear-gradient(180deg, rgba(40, 144, 111, 0) 7.8%, #28906F 56.65%);

        @include responsive.media-breakpoint-down(elg) {
          background: linear-gradient(180deg, rgba(40, 144, 111, 0.00) 20.56%, #28906F 60.77%);
          flex-direction: column;
          height: 100%;
        }
        @include responsive.media-breakpoint-down(sm) {
          padding: 0 16px;
          background: linear-gradient(180deg, rgba(40, 144, 111, 0) 22.31%, #28906f 61.64%);
        }

        @media (max-width: 776px) {
            background: linear-gradient(180deg, rgba(40, 144, 111, 0) 7.8%, #28906F 56.65%);
        }
      }

      .plan-block_step_more {
        top: 29px;
        right: 29px;
        @include responsive.media-breakpoint-down(sm) {
          top: auto;
          right: auto;
        }
      }

      .plan-block_step_title {
        color: var(--white-100);
        display: flex;
        gap: 8px;
        margin-bottom: 16px;
        @include responsive.media-breakpoint-down(sm) {
          margin-bottom: 16px;
          font-size: 20px;
        }
      }

      .plan-block_step_time {
        white-space: nowrap;
        color: var(--green-10);
        @include responsive.media-breakpoint-down(xxl) {
          margin-bottom: 5px;
        }
        @include responsive.media-breakpoint-down(xl) {
          margin-bottom: 12px;
        }
        @include responsive.media-breakpoint-down(elg) {
          margin-bottom: 8px;
        }
        @include responsive.media-breakpoint-down(sm) {
        font-size: 14px;
        }

        img {
          @include responsive.media-breakpoint-down(smd) {
            display: none;
          }
          @include responsive.media-breakpoint-down(sm) {
            display: inline;
          }
        }

        &:before {
          background-image: url("data:image/svg+xml,%3Csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5.83333 1.04199C5.48816 1.04199 5.20833 1.32181 5.20833 1.66699C5.20833 2.01217 5.48816 2.29199 5.83333 2.29199H12.5C12.8452 2.29199 13.125 2.01217 13.125 1.66699C13.125 1.32181 12.8452 1.04199 12.5 1.04199H5.83333Z' fill='%23DBF5ED'/%3E%3Cpath d='M9.79167 6.66699C9.79167 6.32181 9.51185 6.04199 9.16667 6.04199C8.82149 6.04199 8.54167 6.32181 8.54167 6.66699V9.58366C8.54167 10.6192 9.38113 11.4587 10.4167 11.4587H11.6667C12.0118 11.4587 12.2917 11.1788 12.2917 10.8337C12.2917 10.4885 12.0118 10.2087 11.6667 10.2087H10.4167C10.0715 10.2087 9.79167 9.92884 9.79167 9.58366V6.66699Z' fill='%23DBF5ED'/%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M11.9542 2.7675C11.2339 2.70865 10.3377 2.70865 9.19463 2.70866H9.1387C7.9956 2.70865 7.0994 2.70865 6.37911 2.7675C5.64566 2.82743 5.03974 2.95147 4.4913 3.23092C3.58969 3.69031 2.85665 4.42334 2.39726 5.32495C2.11782 5.8734 1.99377 6.47932 1.93384 7.21277C1.87499 7.93306 1.875 8.82926 1.875 9.97236V10.0283C1.875 11.1714 1.87499 12.0676 1.93384 12.7879C1.99377 13.5213 2.11782 14.1273 2.39726 14.6757C2.85665 15.5773 3.58969 16.3103 4.4913 16.7697C5.03974 17.0492 5.64566 17.1732 6.37911 17.2331C7.09939 17.292 7.99557 17.292 9.13864 17.292H9.19465C10.3377 17.292 11.2339 17.292 11.9542 17.2331C12.6877 17.1732 13.2936 17.0492 13.842 16.7697C14.7436 16.3103 15.4767 15.5773 15.9361 14.6757C16.2155 14.1273 16.3396 13.5213 16.3995 12.7879C16.4477 12.198 16.4564 11.4901 16.458 10.6253H17.5C17.8452 10.6253 18.125 10.3455 18.125 10.0003C18.125 9.65515 17.8452 9.37533 17.5 9.37533H16.458C16.4564 8.5105 16.4477 7.80265 16.3995 7.21277C16.3396 6.47932 16.2155 5.8734 15.9361 5.32495C15.4767 4.42334 14.7436 3.69031 13.842 3.23092C13.2936 2.95147 12.6877 2.82743 11.9542 2.7675ZM5.05878 4.34468C5.40164 4.16998 5.82475 4.06696 6.4809 4.01335C7.14435 3.95914 7.98958 3.95866 9.16667 3.95866C10.3438 3.95866 11.189 3.95914 11.8524 4.01335C12.5086 4.06696 12.9317 4.16998 13.2745 4.34468C13.941 4.68423 14.4828 5.22603 14.8223 5.89244C14.997 6.2353 15.1 6.65841 15.1536 7.31456C15.2078 7.97801 15.2083 8.82324 15.2083 10.0003C15.2083 11.1774 15.2078 12.0226 15.1536 12.6861C15.1 13.3422 14.997 13.7654 14.8223 14.1082C14.4828 14.7746 13.941 15.3164 13.2745 15.656C12.9317 15.8307 12.5086 15.9337 11.8524 15.9873C11.189 16.0415 10.3438 16.042 9.16667 16.042C7.98958 16.042 7.14435 16.0415 6.4809 15.9873C5.82475 15.9337 5.40164 15.8307 5.05878 15.656C4.39238 15.3164 3.85057 14.7746 3.51102 14.1082C3.33632 13.7654 3.2333 13.3422 3.17969 12.6861C3.12549 12.0226 3.125 11.1774 3.125 10.0003C3.125 8.82324 3.12549 7.97801 3.17969 7.31456C3.2333 6.65841 3.33632 6.2353 3.51102 5.89244C3.85057 5.22603 4.39238 4.68423 5.05878 4.34468Z' fill='%23DBF5ED'/%3E%3Cpath d='M5.83333 17.7087C5.48816 17.7087 5.20833 17.9885 5.20833 18.3337C5.20833 18.6788 5.48816 18.9587 5.83333 18.9587H12.5C12.8452 18.9587 13.125 18.6788 13.125 18.3337C13.125 17.9885 12.8452 17.7087 12.5 17.7087H5.83333Z' fill='%23DBF5ED'/%3E%3C/svg%3E%0A");
          @include responsive.media-breakpoint-down(sm) {
            content: "";
          }
        }
        span {
          @include responsive.media-breakpoint-down(sm) {
            display: inline;
          }
        }
      }

      .plan-block_step_bg {
        padding-top: 45px;
        @include responsive.media-breakpoint-down(sm) {
          position: absolute;
          top: 0;
          left: 0;
          height: 100%;
          justify-content: center;
          padding-top: 6px;
        }
      }

      .plan-block_step_num__number {
        color: var(--green-100);
        padding: 0 20px;
        border-radius: 1000px;
        border: 2px solid var(--green-100);
        background: var(--green-10);
        @include responsive.media-breakpoint-down(sm) {
          padding: 10px 16px;
          font-size: 14px;
        }
      }

      .plan-block_step_num__text {
        background: var(--white-100);
        color: var(--green-100);
        @include responsive.media-breakpoint-down(sm) {
          padding: 10px 16px;
          font-size: 14px;
          height: auto;
          margin-top: 1px;
          border: 2px solid var(--green-100);
        }
      }

      .plan-block_step_num {
        top: 29px;
        left: 29px;
        @include responsive.media-breakpoint-down(xl) {
          top: 21px;
          left: 21px;
        }
        @include responsive.media-breakpoint-down(sm) {
          position: absolute;
          left: 16px;
          top: 0;
        }
      }
    }
  }

  &_arrow {
    position: absolute;
    top: 124px;
    left: 70px;
    @include responsive.media-breakpoint-down(xxl) {
      top: 154px;
      left: 30px;
    }
    @include responsive.media-breakpoint-down(xl) {
      display: none;
    }

    img {
      margin-left: 16px;
    }

    &_text {
      width: 168.228px;
      transform: rotate(9.96deg);
      color: var(--white-100);
      text-align: center;
      font-size: 14px;
      font-style: normal;
      font-weight: 600;
      line-height: 120%; /* 16.8px */
    }
  }

  &_scrolbar.swiper-scrollbar {
    display: none;

    @include responsive.media-breakpoint-down(sm) {
      display: block;
      z-index: 1;
      width: 100%;
      height: 8px;
      background-color: rgba(255, 255, 255, 0.1);
      border-radius: 10px;
      position: unset !important;
      margin-top: 24px;
    }
  }

  .swiper-scrollbar-drag {
    background-color: var(--white-100);
    border-radius: 10px;
  }

  &_modal {
    display: flex;
    padding-right: 80px;
    gap: 48px;
    @include responsive.media-breakpoint-down(elg) {
      padding-right: 56px;
    }
    @include responsive.media-breakpoint-down(smd) {
      padding-right: 0;
      flex-direction: column;
      gap: 20px;
    }

    &.green {
      --plan-step-number-bg: var(--green-100);
      --plan-step-number-text-bg: var(--green-10);
      --plan-step-number-text-color: var(--green-100)
    }

    &_titles {
      display: flex;
      flex-direction: column;
      max-width: 400px;
      width: 100%;
      @include responsive.media-breakpoint-down(xl) {
        max-width: 320px;
      }
      @include responsive.media-breakpoint-down(elg) {
        max-width: 260px;
      }
      @include responsive.media-breakpoint-down(smd) {
        max-width: 100%;
      }

      .plan-block_step_num {
        position: relative;
        top: auto;
        left: auto;
        margin-top: auto;
        @include responsive.media-breakpoint-down(smd) {
          order: 1;
          margin-bottom: 20px;
        }

        &.last {
          .plan-block_step_num__text {
            border: 2px solid var(--green-100);
            display: block !important;
          }
        }
      }
    }

    &_title {
      color: var(--black-100);
      font-size: 32px;
      font-style: normal;
      font-weight: 600;
      line-height: 120%;
      margin-bottom: 12px;
      @include responsive.media-breakpoint-down(xl) {
        font-size: 24px;
      }
      @include responsive.media-breakpoint-down(smd) {
        order: 2;
        margin-bottom: 8px;
      }
    }

    &_time {
      color: var(--grey-100);
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: 140%;
      display: flex;
      gap: 6px;
      margin-bottom: 12px;
      @include responsive.media-breakpoint-down(smd) {
        order: 3;
        font-size: 14px;
        margin-bottom: 0;
      }

      &:before {
        width: 20px;
        height: 20px;
        display: block;
        @include responsive.media-breakpoint-down(smd) {
          width: 16px;
          height: 16px;
        }
      }
    }

    &_content {
      color: var(--black-100);
      font-feature-settings: 'ss12' on;
      font-size: 20px;
      font-style: normal;
      font-weight: 400;
      line-height: 140%;
      @include responsive.media-breakpoint-down(xl) {
        font-size: 16px;
      }

      p {
        margin-bottom: 22px;

        &:last-child {
          margin-bottom: 0;
        }
      }

      .button {
        max-width: 300px;
        @include responsive.media-breakpoint-down(sm) {
          max-width: 100%;
        }
      }
    }
  }

  &_form {
    @include responsive.media-breakpoint-down(lg) {
      margin: 0 -8px;
    }
    @include responsive.media-breakpoint-down(sm) {
      margin: 0;
    }

    &_input {
      background: transparent;
      @include responsive.media-breakpoint-down(elg) {
        width: calc(50% - 4px);
      }
      @include responsive.media-breakpoint-down(sm) {
        width: 100%;
      }

      .input-container_errorMark {
        top: 12px;
        right: 14px;
      }

      input {
        padding: 14px 16px;
        border-radius: 12px;
        border: 1.2px solid var(--border-color, var(--green-10));
        background: rgba(255, 255, 255, 0.10);
        backdrop-filter: blur(8px);
        color: var(--white-100);

        &::placeholder {
          color: var(--white-50, rgba(255, 255, 255, 0.60));
          font-size: 14px;
          font-style: normal;
          font-weight: 400;
          line-height: 140%;
        }
      }
    }

    &_submit {
      color: var(--green-100);
      font-size: 14px;
      font-style: normal;
      font-weight: 600;
      line-height: 140%;
      padding: 14px 24px;
      border-radius: 12px;
      background-color: var(--white-100);
      transition: background-color .1s ease-in-out, color .1s ease-in-out;

      @include responsive.media-breakpoint-down(elg) {
        width: 100%;
        margin-top: 4px;
      }

      @media (any-hover: hover) {
        &:hover {
            background-color: var(--grey-10);
        }
      }
    }
  }
}

.plan-block_form {
    .plan-block_form_top {
        display: flex;
        flex-wrap: wrap;
        gap: 8px;

        .input-container {
            width: 100%;

            @media (min-width: 775px) {
                width: calc(50% - 8px);
            }

            .plan-block_form_input {
                width: 100%;
            }
        }

        .plan-block_form_submit {
            width: 100%;

            &.focus-visible {
                outline: 4px dashed rgba(37, 87, 246, 1);
                outline-offset: 2px;
            }
        }
    }

    .checkbox__wrapper {
        margin-top: 12px;

        @media (min-width: 577px) {
            margin-top: 16px;
        }
    }
}

.plan-block_step.swiper-slide {
    @media (max-width: 576px) {
        &:not(:last-of-type) {
            margin-right: 12px;
        }
    }
}

.input-container_errorMark--show {
    --tooltip-opasity: 1;
}
