@use '../mixins/responsive';

.header {
  position: relative;
  z-index: 10;
  background-color: var(--white-100);
  transition: box-shadow .3s ease-in-out;

  @include responsive.media-breakpoint-down(sm) {
    position: sticky;
    top: 0;
    padding-bottom: 10px;
  }

  &-nav {
    display: flex;
    gap: 32px;
    position: relative;
    @include responsive.media-breakpoint-down(xl) {
      gap: 24px;
    }

    @include responsive.media-breakpoint-down(elg) {
      display: none;
    }

    &_item {
      color: var(--black-100);
      font-size: 14px;
      font-style: normal;
      font-weight: 500;
      line-height: 1.4;
      transition: color 0.1s ease-in-out;
      display: flex;
      align-items: center;
      gap: 4px;

      &:after {
        display: inline-block;
        width: 16px;
        height: 16px;
      }

      @media (any-hover: hover) {
        &:hover {
            color: var(--green-100);
        }
      }

      &__select {
        &:hover {
          .header-nav_sub-list {
            opacity: 1;
            pointer-events: auto;
            z-index: 3;
          }
        }
      }
    }

    &_sub {
      &-list {
        pointer-events: none;
        opacity: 0;
        position: absolute;
        top: 100%;

        &_container {
            overflow: hidden;
            margin-top: 8px;
            padding: 4px 0;
            width: 160px;
            display: flex;
            flex-direction: column;
            border-radius: 20px;
            background-color: var(--white-100);
            box-shadow: 0 12px 40px 0 rgba(14, 15, 17, 0.05);
            transition: transform .1s ease-in-out;
        }
      }

      &_item {
        padding: 8px 16px 11px;

        &:hover {
          background: var(--Card-Background);
        }
      }
    }
  }

  &-contacts {
    display: flex;
    align-items: center;
    gap: 24px;
    position: relative;
  }

  &-contact {
    color: var(--black-100);
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 140%;
    display: flex;
    gap: 4px;
    align-items: center;
    white-space: nowrap;
    // margin-bottom: 8px;

    &:before {
      display: block;
      width: 16px;
      height: 16px;
    }

    &__link {
        transition: color 0.1s ease-in-out;

        @media (any-hover: hover) {
            &:hover {
                color: var(--grey-100);
            }
        }
    }
  }

  &-logo {
    position: relative;
    height: 42px;


    &_desc {
      color: var(--grey-100);
      font-size: 14px;
      font-style: normal;
      font-weight: 500;
      line-height: 140%;
    }
  }
}

.header--sticky {
    @media (max-width: 576px) {
        box-shadow: -1px 3px 11px 0px rgba(0, 26, 52, 0.1215686275);
    }
}

.header-nav_item-decoration {
    position: relative;
    height: 100%;
    width: 24px;
    margin-left: 4px;

    svg {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 24px;
        height: 24px;
    }
}

.header-nav_sub-list_container {
    box-shadow: 0px 16px 34px 0px #001A341F;
}
