      left: 0;
      //animation: horizontalAnim 2s infinite both cubic-bezier(0, 0, .63, .9);
      transform-origin: 0 0;
      z-index: 1;

      &:nth-child(2) {
        margin-left: 11px;
        animation-delay: 0.5s;

        &:before {
          animation-delay: 0.5s;
        }
      }

      &:nth-child(3) {
        margin-left: 11px;
        animation-delay: 1.0s;

        &:before {
          animation-delay: 1.0s;
        }
      }

      &:nth-child(4) {
        margin-left: 11px;
        animation-delay: 1.5s;

        &:before {
          animation-delay: 1.5s;
        }
      }
    }

