      //animation: verticalAnim 2s infinite both cubic-bezier(.21, .01, .2, .96);
      animation: verticalAnim 2s infinite both cubic-bezier(0.41, 0.48, 0.43, 0.45);;
      transform-origin: 0 0;
      content: '';
      position: absolute;
      width: 28px;
      height: 28px;
      border-radius: 50%;
      top: 0;
    }

    .percent-circle {
      display: flex;
      gap: 25px;
      flex-direction: column;
      position: absolute;
      z-index: -1;
      right: 48px;
      bottom: -170px;

      &_right {
        display: flex;
        gap: 25px;
        flex-direction: column;
        position: absolute;
        z-index: -1;
        right: 20px;
        bottom: -100px;

        .percent-left {
          &:nth-child(2) {
            margin-left: 11px;
            animation-delay: 0.5s;

            &:before {
              animation-delay: 0.5s;
            }
          }
        }

        .percent-left:before {
          animation: verticalAnimAsist 1s infinite both cubic-bezier(0.41, 0.48, 0.43, 0.45);;
