      }
    }
  }
}

.main-banner_discount-btn {
    pointer-events: all;
    border-radius: 12px 12px 0 0;
    padding: 10px 16px;
    background: var(--red-100, #F24C41);
    color: var(--white-100);
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 140%;
    transform: rotate(-90deg) translateX(-40%) translateY(202%);
    width: max-content;
    box-shadow: 0px 10px 34px rgb(0 26 52 / 24%);
    transition: color .1s ease-in-out;

    @include responsive.media-breakpoint-down(smd) {
        padding: 10px 14px;
        font-size: 12px;
        font-weight: 700;
    }

    @media (any-hover: hover) {
        &:hover {
            background: var(--red-100, #F24C41);
        }
    }

    &.focus-visible {
        outline: 4px dashed rgba(37, 87, 246, 1);
        outline-offset: 2px;
    }
}

.main-banner_discount_container {
  position: fixed;
  z-index: 11;
  top: 200px;
  right: -1px;
  pointer-events: none;

  @media (max-width: 1024px) {
    z-index: 9;
  }
}

.main-banner_discount_container .percent-left:before {
  box-shadow: 0px 0px 34px rgb(0 26 52 / 14%);
}
