.modal__opened,
.modal__shadow {
    position: fixed;
    top: -2px;
    right: 0;
    bottom: -2px;
    left: 0;
    overflow: hidden;
}

.modal__shadow {
    border: none;
    display: block;
    width: 100%;
    top: -2px;
    right: 0;
    bottom: -2px;
    left: 0;
    pointer-events: none;
    z-index: 98;
    opacity: 0;
    transition: opacity 0.15s ease;
    background-color: rgba(0 16 32 / 60%);
    user-select: none;
}

.modal__shadow--show {
    pointer-events: auto;
    opacity: 1;
}

.modal {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    overflow: hidden;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    opacity: 1;
    pointer-events: none;
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-start;
    z-index: 999;
    visibility: hidden;
    transition: visibility 0s linear 0.2s;
}

.modal--active {
    opacity: 1;
    transition: visibility 0s linear 0s;
}

.modal--active,
.modal--moved {
    pointer-events: auto;
    visibility: visible;
}

.modal__wrap {
    flex-shrink: 0;
    flex-grow: 0;
    width: 100%;
    min-height: 100%;
    margin: auto;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    justify-content: center;
}

.modal__window {
    margin: 50px 0;
    box-sizing: border-box;
    flex-shrink: 0;
    flex-grow: 0;
    background: #fff;
    padding: 32px;
    width: 100%;
    max-width: 400px;
    overflow: visible;
    transition: transform 0.2s ease 0s, opacity 0.2s ease 0s;
    transform: scale(0.9);
    opacity: 0;
    border-radius: 24px;

    .modal-small & {
        max-width: 378px;
    }

    .modal-big-small & {
        max-width: 395px;
    }

    .modal-medium & {
        max-width: 506px;
    }

    .modal-large & {
        max-width: 680px;
    }
}

// Отключить для появления полоски как на айфонах
.modal {
    .modal__window {
        &::before,
        &::after {
            content: none !important;
        }
    }
}

.modal--active .modal__window {
    transform: scale(1);
    opacity: 1;
}

@media (max-width: 767px) {
    .modal__window {
        margin: 0;
    }
}

.modal__close {
    position: absolute;
    top: 0;
    right: 0;
    width: 24px;
    height: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: translateX(100%) translateY(-100%);
    z-index: 1;
    padding: 0;
    border: 0;
    background: transparent;

    @media (any-hover: hover) {
        &:hover {
            .modal__close-icon svg path {
                fill: rgba(255 255 255 / 70%);
            }
        }
    }

    @media (min-width: 577px) {
        width: 32px;
        height: 32px;
    }
}

.modal__close-icon {
    width: 24px;
    height: 24px;
    display: flex;

    svg path {
        fill: rgba(255, 255, 255, 0.6);
        transition: fill 0.15s ease-in-out;
    }

    @media (min-width: 577px) {
        width: 32px;
        height: 32px;
    }
}

.modal__header {
    display: flex;
    flex-direction: column;
    margin-bottom: 24px;
}

.modal__title {
    padding-right: 25px;
    font-size: 24px;
    font-weight: 500;
    line-height: 1.3;
    color: #000;

    @media (min-width: 577px) {
        padding-right: 0;
    }
}

.modal__description {
    margin-top: 6px;
    display: block;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.43;
}

.modal__scroll-element {
    width: calc(100% + 32px);
    margin-left: -16px;
    padding-right: 16px;
    padding-left: 16px;
}

// MODIFICATIONS

