.modal-primary {
    @media (max-width: 576px) {
        height: calc((var(--vh, 1vh) * 100));
        overflow: hidden;
        will-change: top;

        .modal__body {
            height: 100%;

            &::after {
                content: "";
                position: absolute;
                left: 0;
                bottom: 2px;
                height: 100px;
                width: 100%;
                transform: translateY(100%);
                background-color: #fff;
            }

            .tab-tabs {
                height: 100%;
            }

            .tab-buttons {
                margin-bottom: 0;
            }

            .tab-tabs__list {
                padding-top: 24px;
                padding-bottom: 90px;
            }
        }

        .tab-tabs__list.modal__scroll-element {
            height: calc(100% - 56px);
            overflow-x: hidden;
            overflow-y: scroll;
            -webkit-overflow-scrolling: touch;
        }

        .modal__window {
            position: fixed;
            max-height: calc(
                100vh - 56px
            ); /* Fallback for browsers that do not support Custom Properties */
            max-height: calc((var(--vh, 1vh) * 100) - 56px);
            bottom: 0;
            max-width: 100%;
            padding: 32px 24px 40px;
            border-radius: 24px 24px 0px 0px;
            opacity: 1;
            transform: scale(1) translateY(100%);

            transition: transform 0.2s ease-in-out;

            &::before {
                content: "";
                width: 42px;
                height: 22px;
                position: absolute;
                left: 50%;
                top: 0px;
                transform: translate(-50%, -100%);
            }

            &::after {
                content: "";
                width: 32px;
                height: 6px;
                position: absolute;
                top: -6px;
                left: 50%;
                transform: translate(-50%, -100%);
                border-radius: 32px;
                background-color: #fff;
                transition: background-color 0.18s ease-in-out,
                    width 0.18s ease-in-out;
            }
        }

        &.modal--active .modal__window {
            transform: scale(1) translateY(0%);
        }

        .modal__close {
            top: -12px;
            right: 16px;
            transform: translateY(-100%);
        }

        .modal__window--animated {
            transition: top 0.175s ease-in-out, transform 0.1s ease-in-out !important;
        }

        .modal__window--dragged {
            @media (max-width: 576px) {
                &::after {
                    width: 48px;
                    background-color: rgb(255 255 255 / 65%);
                }
            }
        }
    }
}

