body {
  width: 100%;
  min-width: 320px;
  background-color: var(--Card-Background);
  font-family: "SF UI Display", sans-serif;

  &.hidden {
    position: fixed;
    overflow-y: scroll;
    width: 100%;
  }
}

main {
  position: relative;
  flex: 1 0 auto;
}

a {
  cursor: pointer;
  text-decoration: none;
}

* {
  font-family: 'SF UI Display', sans-serif !important;
}

.body--block-scroll {
  position: fixed;
  right: 0;
  left: 0;
  overflow: hidden;
}

.visually-hidden {
    position: absolute !important;
    overflow: hidden;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    border: 0;
    clip: rect(0 0 0 0);
    word-wrap: normal !important;
}

.wrapper {
  margin: 0 auto;
  max-width: 1440px;
  padding-left: 24px;
  padding-right: 24px;
  @include responsive.media-breakpoint-down(xl) {
    padding-left: 32px;
    padding-right: 32px;
  }
  @include responsive.media-breakpoint-down(lg) {
    padding-left: 0;
    padding-right: 0;
  }
}

.page-container {
  margin: 0 auto;
  max-width: 1440px;
  padding-left: 60px;
  padding-right: 60px;
  @include responsive.media-breakpoint-down(xl) {
    padding-left: 32px;
    padding-right: 32px;
  }
  @include responsive.media-breakpoint-down(smd) {
    padding-left: 16px;
    padding-right: 16px;
  }
}

.hide_elg {
  @include responsive.media-breakpoint-down(elg) {
    display: none;
  }
}
.hide_md {
  @include responsive.media-breakpoint-down(smd) {
    display: none;
  }
}

.page-title__main {
  color: var(--black-100);
  font-size: 56px;
  font-style: normal;
  font-weight: 600;
  line-height: 100%; /* 56px */

  @include responsive.media-breakpoint-down(xl) {
    font-size: 48px;
  }
  @include responsive.media-breakpoint-down(smd) {
    font-size: 32px;
  }
  span {
    color: var(--green-100);
  }
}

.page-title__grey {
  font-weight: 400;
    font-size: 16px;
    line-height: 22.4px;
    color: var(--grey-50);
    z-index: 1;

    @include responsive.media-breakpoint-down(sm) {
      font-size: 14px;
      line-height: 19.6px;
    }
}

.swiper-button-lock {
  opacity: 0;
  pointer-events: none;
}

.swiper-button-next, .swiper-button-prev {
  &:after {
    content: none !important;
  }
}

.checkbox__wrapper {
    display: flex;
}

.checkbox {
    position: absolute;
    z-index: -1;
    opacity: 0;

    &:checked {
        & ~ .checkbox__label {
            &::after {
                opacity: 1;
                transform: scale(1) translateX(0) translateY(0);
            }
        }
    }
}


.checkbox__label {
    position: relative;
    padding-left: 30px;

    font-size: 14px;
    font-weight: 400;
    line-height: 1.4;
    user-select: none;
    cursor: unset;

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        display: inline-flex;
        width: 20px;
        height: 20px;
        margin-right: 10px;
        flex-shrink: 0;
        flex-grow: 0;
        border-width: 1px;
        border-style: solid;
        border-radius: 6px;
        cursor: pointer;
        transition:
            background-color 0.15s ease-in-out,
            border-color 0.15s ease-in-out;
    }

    &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 20px;
        height: 20px;
        background-repeat: no-repeat;
        background-position: center center;
        opacity: 0;
        transform: scale(0.8) translateX(-2px) translateY(5px);
        cursor: pointer;
        transition:
            opacity 0.15s ease-in-out,
            transform 0.15s ease-in-out;
    }
}

.checkbox__label-link {
    cursor: pointer;
    transition: color 0.1s ease-in-out;
}

.checkbox--green {
    & ~ .checkbox__label {
        color: #8F959B;

        .checkbox__label-link {
            color: #28906F;

            @media (any-hover: hover) {
                &:hover {
                    color: #1A8362;
                }
            }

            &.focus-visible {
                outline: 3px dashed rgba(37, 87, 246, 1);
                outline-offset: 1px;
            }
        }

        &::before {
            border-color: #E7EDF3;
        }

        &::after {
            background-image: url("data:image/svg+xml;utf8,<svg width='10' height='8' viewBox='0 0 10 8' fill='none' xmlns='http://www.w3.org/2000/svg'><path d='M9.47141 1.80473C9.73176 1.54438 9.73176 1.12227 9.47141 0.861919C9.21107 0.601569 8.78896 0.601569 8.52861 0.861919L3.66668 5.72385L1.47141 3.52859C1.21107 3.26824 0.788955 3.26824 0.528606 3.52859C0.268256 3.78893 0.268256 4.21104 0.528606 4.47139L3.19527 7.13806C3.45562 7.39841 3.87773 7.39841 4.13808 7.13806L9.47141 1.80473Z' fill='rgb(255 255 255)'/></svg>");
        }
    }

    &:checked {
        & ~ .checkbox__label {
            &::before {
                background-color: #28906F;
                border-color: transparent;
            }
        }
    }

    @media (any-hover: hover) {
        &:not(:disabled):hover {
            & ~ .checkbox__label {
                &::before {
                    border-color: #8F959B;
                }
            }

            &:checked {
                & ~ .checkbox__label {
                    &::before {
                        background-color: #1A8362;
                        border-color: transparent;
                    }
                }
            }
        }
    }

    &.focus-visible {
        & ~ .checkbox__label {
            &::before {
                outline: 3px dashed rgba(37, 87, 246, 1);
                outline-offset: 2px;
            }
        }
    }
}

