.button__loader {
    opacity: 0;
    width: 0px;
    height: 0px;
    margin-right: 0px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: opacity 0.2s ease, height 0.2s ease, width 0.2s ease, margin-right .2s ease;

    svg {
        width: 100%;
        height: 100%;
        transform-origin: center;
    }
}

.button__loader.active {
    opacity: 1;
    width: 16px;
    height: 16px;
    margin-right: 8px;
}

.button__loader.loading {
    svg {
        height: 100%;
        width: 100%;
        animation: rotateLoader 1s linear infinite;
    }
}

@keyframes rotateLoader {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.skip-link {
    position: absolute;
    z-index: 999;
    top: -200px;
    padding: 10px 20px;

    font-size: 24px;
    color: #fff;

    border-bottom-right-radius: 30px;
    background-color: #28906F;

    &.focus-visible {
        top: 0;
        outline: 2px dashed #eeeff1;
        outline-offset: -3px;
    }
}

.modal__input-textarea {
    width: 100%;
    height: 146px;
    color: var(--black-100);
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    border: 1.2px solid var(--border-color, var(--grey-10));
    padding: 19px 20px;
    border-radius: 16px;
    resize: none;
    height: 144px;

    &::placeholder {
        color: #8F959B;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 140%;
        transition: color .1s ease-in-out;
    }

    .error & {
        &::placeholder {
            color: rgba(242, 76, 65, 1);
        }
    }
}

.footer-developer {
    width: 100%;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #262729;
}

.footer-developer__text {
    display: inline-flex;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    text-align: left;

    span {
        color: #FFFFFF99;
    }

    a {
        font-weight: 600;
        color: #FFFFFF;
        transition: color .1s ease-in-out;

        @media (any-hover: hover) {
            &:hover {
                color: #ffffffc5;
            }
        }
    }
}

.question-icon {
    -webkit-tap-highlight-color: transparent;

    &:focus {
        outline: none;
    }
    &.focus-visible {
        outline: 3px dashed #28906F;
    }
}
