@use '../mixins/responsive';

.button {
  width: 100%;
  border-radius: 12px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: background-color .1s ease-in-out, color .1s ease-in-out;

  @include responsive.media-breakpoint-down(smxs) {
    font-size: 13px;
  }

  &.light-green {
    background-color: var(--green-10);
    color: var(--green-100);

    span {
      color: var(--black-100) !important;
    }

    @media (any-hover: hover) {
      &:hover {
        background-color: rgba(200, 236, 225, 1);
      }
    }
  }

  &.green {
    background-color: var(--green-100);
    color: var(--white-100);

    @media (any-hover: hover) {
      &:hover {
        background-color: rgba(26, 131, 98, 1);
      }
    }
  }

  &.grey {
    background-color: var(--grey-10);
    width: fit-content;
    padding: 0 20px;
    color: var(--black-100);

    @include responsive.media-breakpoint-down(sm) {
      width: 100%;
    }

    @media (any-hover: hover) {
      &:hover {
        background-color: rgba(221, 228, 234, 1);
      }
    }
  }

  &.blue {
    background-color: #DEE7FF;
    height: 48px;
    color: var(--blue-100);
    width: fit-content;
    padding: 0 20px;

    @include responsive.media-breakpoint-down(sm) {
      padding: unset;
      width: 100%;
    }

    @media (any-hover: hover) {
      &:hover {
        background-color: #C8D7FF;
      }
    }
  }

  &.black {
    background-color: var(--black-100);
    height: 64px;
    color: var(--white-100);

    @include responsive.media-breakpoint-down(xl) {
      height: 60px;
    }

    @include responsive.media-breakpoint-down(sm) {
      height: 48px;
    }

    @media (any-hover: hover) {
      &:hover {
        color: var(--white-50);
      }
    }
  }

    &:disabled {
        color: rgba(143, 149, 155, 1);
        background-color: rgba(231, 237, 243, 1);
        cursor: default;
    }
}

.button-big {
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 120%;
  padding: 22.5px 32px;
  border-radius: 16px;
  transition: background-color .1s ease-in-out, color .1s ease-in-out;

  @include responsive.media-breakpoint-down(lg) {
    padding: 14px 24px;
    font-size: 14px;
    line-height: 140%;
  }

  &.blue {
    color: var(--white-100);
    background: var(--blue-100);

    &:hover {
      background: #1146EF;
    }
  }
}

a,
button {
    &.focus-visible {
        outline: 3px dashed #28906F;
        outline-offset: 4px;
    }
}
