@use '../mixins/responsive';

.articles {
  .page-container {
    position: relative;
  }

  .page-container-right-left {
    margin-top: 60px;
    margin-bottom: 60px;

    @include responsive.media-breakpoint-down(xl) {
      margin-top: 32px;
      margin-bottom: 32px;
    }

    @include responsive.media-breakpoint-down(sm) {
      margin-top: 24px;
      margin-bottom: 32px;
    }
  }

  .page-title__main {
    @include responsive.media-breakpoint-down(sm) {
      text-align: center;
    }
  }

  &__top-title {
    font-size: 14px;
    font-weight: 400;
    line-height: 1;
    text-transform: uppercase;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
  }

  .circle-black {
    background-color: var(--black-100);
    width: 8px;
    height: 8px;
    border-radius: 50%;
  }

  &__is-new {
    color: var(--green-100) !important;
  }

  &__title, strong {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 4;
    font-size: 20px;
    line-height: 28px;

    @include responsive.media-breakpoint-down(xl) {
      font-size: 16px;
      line-height: 22.4px;
    }
  }

  .swiper {
    width: 100%;
  }

  .swiper-slide {
    border-radius: 24px;
    background-color: var(--white-100);
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    max-width: 321px;
    transition: transform 5s;

    &:not(:last-of-type) {
        margin-right: 12px;
    }

    @include responsive.media-breakpoint-down(xl) {
      max-width: 312px;
    }

    @include responsive.media-breakpoint-down(lg) {
      max-width: 334px;
    }

    @include responsive.media-breakpoint-down(sm) {
      width: 300px;
    }

    @include responsive.media-breakpoint-down(smxs) {
      width: 100%;
    }

    &__top-content {
      display: flex;
      align-items: flex-start;
      justify-content: flex-start;
      flex-direction: column;
      gap: 20px;
      padding: 32px 32px 0;

      @include responsive.media-breakpoint-down(xl) {
        padding: 24px 24px 0;
      }

      @include responsive.media-breakpoint-down(lg) {
        padding: 24px 24px 0;
      }

      strong {
        font-weight: 600;
      }

      span {
        font-size: 16px;
        line-height: 22.4px;
        color: var(--grey-100);

        @include responsive.media-breakpoint-down(sm) {
          font-size: 14px;
          line-height: 19.6px;
        }
      }
    }

    &__bottom-content {
      object-fit: cover;
      position: relative;

      img {
        transition: transform .3s;
      }
    }

    .bottom-content__icon-for-img {
      position: absolute;
      right: 20px;
      bottom: 20px;
      background-color: var(--white-100);
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      min-width: 52px;
      min-height: 76px;
      border-radius: 1000px;
      font-size: 12px;
      line-height: 1;
      font-weight: 700;
      z-index: 1;

      @include responsive.media-breakpoint-down(xl) {
        right: 16px;
        bottom: 16px;
      }

      svg {
        margin-bottom: 6px;
      }
    }

    @media (any-hover: hover) {
      &:hover .swiper-slide__bottom-content img {
        transform: scale(1.1);
      }
    }
  }

  .swiper-slide__bottom-content {
    img {
      width: 100%;
    }
  }

  .swiper-scrollbar {
    display: none;

    @include responsive.media-breakpoint-down(xl) {
      display: block;
      z-index: 1;
      width: 100%;
      height: 8px;
      background-color:var(--white-100);
      border-radius: 10px;
      position: unset !important;
    }
  }

  .swiper-scrollbar-drag {
    background-color: var(--green-100);
    border-radius: 10px;
  }

  .arrows-right-left {
    position: absolute;
    right: 41px;
    bottom: 40%;
    z-index: 2;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    gap: 4px;

    @include responsive.media-breakpoint-down(xl) {
      display: none;
    }
  }

  .arrow {
    background-color: var(--green-100);
    box-shadow: 1px 8px 20px 0 rgba(12, 13, 16, 0.08);
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    border-radius: 10px;

    &:nth-child(2) {
      transform: rotate(180deg);
    }

    &-left, &-right {
      transition: transform .3s;

      &:not(.swiper-button-disabled) {
        &:hover {
          background-color: rgba(26, 131, 98, 1);
        }
      }
    }
  }

  .grey.articles {
    display: none;

    @include responsive.media-breakpoint-down(sm) {
      display: flex;
      margin-top: 24px;
    }
  }

  .swiper {
    margin-bottom: 30px;
    isolation: isolate;

    @media (max-width: 1199px) {
        margin-left: -32px;
        padding-left: 32px;
        padding-right: 32px;
        width: calc(100% + 64px);
    }

    @media (max-width: 744px) {
        margin-left: -16px;
        padding-left: 16px;
        padding-right: 16px;
        width: calc(100% + 32px);
    }

    @media (min-width: 1200px) {
        border-radius: 24px;
    }

    @include responsive.media-breakpoint-down(xl) {
      margin-bottom: 32px;
    }

    @include responsive.media-breakpoint-down(lg) {
      margin-bottom: 24px;
    }
  }

    .tab-tabs__item {
        position: relative;
    }

    .swiper__navigation {
        position: absolute;
        z-index: 2;
        right: 0;
        top: 50%;
        transform: translateX(50%) translateY(calc(-50% - 30px));
        display: flex;
        flex-direction: column;
        gap: 4px;
    }

    .swiper__button--primary {
        width: 40px;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 10px;
        background-color: rgba(40, 144, 111, 1);
        transition: background-color .1s ease-in-out;

        @media (any-hover: hover) {
            &:hover {
                background-color: rgba(26, 131, 98, 1);
            }
        }

        svg {
            width: 20px;
            height: 20px;
        }

        &.swiper-button-disabled {
            background-color: rgb(156 219 199);
        }
    }

    .swiper__button-prev {

    }

    .swiper__button-next {
        svg {
            transform: rotate(180deg);
        }
    }
}

.articles__header {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .tab-buttons {
        width: min-content;
        max-width: 100%;

        @media (min-width: 577px) {
            max-width: calc(100% - 170px);
        }
    }

    .tab-buttons__container {
        width: 100%;
    }

    .articles__link-to-all {
        flex-shrink: 0;

        @media (max-width: 576px) {
            display: none;
        }
    }
}

.article-card {
    border-radius: 24px;
    background-color: var(--white-100);
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    max-width: 321px;
    transition: transform 5s;

    @include responsive.media-breakpoint-down(xl) {
      max-width: 312px;
    }

    @include responsive.media-breakpoint-down(lg) {
      max-width: 334px;
    }

    @include responsive.media-breakpoint-down(sm) {
      width: 300px;
    }

    @include responsive.media-breakpoint-down(smxs) {
      width: 100%;
    }

    @media (any-hover: hover) {
        &:hover {
            .article-card__bottom-content img {
                transform: scale(1.1);
            }
        }
    }
}

.article-card__top-content {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;
    gap: 20px;
    padding: 32px 32px 0;

    @include responsive.media-breakpoint-down(xl) {
      padding: 24px 24px 0;
    }

    @include responsive.media-breakpoint-down(lg) {
      padding: 24px 24px 0;
    }

    strong {
      font-weight: 600;
    }
}

.article-card__category {
    height: 22.4px;
    display: flex;
    align-items: center;
    gap: 10px;

    &::before {
        content: '';
        background-color: #0E0F11;
        background-color: var(--black-100);
        width: 8px;
        height: 8px;
        border-radius: 50%;
    }
}

.article-card__category-text {
    color: #0E0F11;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 400;
    line-height: 1;
}

.article-card__bottom-content {
    object-fit: cover;
    position: relative;

    img {
        aspect-ratio: 3/2;
        object-fit: cover;
        width: 100%;
        transition: transform .3s;
    }
}

.article-card__icon-for-img {
    position: absolute;
    right: 20px;
    bottom: 20px;
    background-color: var(--white-100);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    min-width: 52px;
    min-height: 76px;
    border-radius: 1000px;
    font-size: 12px;
    line-height: 1;
    font-weight: 700;
    z-index: 1;
    color: #0E0F11;

    @include responsive.media-breakpoint-down(xl) {
      right: 16px;
      bottom: 16px;
    }

    svg {
      margin-bottom: 6px;
    }
}

.article-card__is-new {
    font-size: 16px;
    line-height: 22.4px;
    color: #28906F;
}

.article-card__title {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 4;
    font-size: 20px;
    line-height: 28px;
    color: #0E0F11;

    strong {
        display: inline;
    }

    @include responsive.media-breakpoint-down(xl) {
        font-size: 16px;
        line-height: 22.4px;
    }
}

.article-card__info {
    display: inline-flex;
    align-items: baseline;
    font-size: 16px;
    line-height: 22.4px;
    color: #8F959B;
    color: var(--grey-100);

    strong {
        font-size: inherit;
        font-weight: 600;
        color: #28906F;
        margin-right: 4px;
    }
}

.articles .swiper-slide {
    height: auto;

    .article-card {
      height: 100%;
    }
}

.article-card__bottom-content {
  margin-top: 40px;
}
