@use '../mixins/responsive';

.header-bottom {
  display: flex;
  gap: 24px;
  padding-bottom: 20px;
  width: 100%;
  overflow: auto;

  @include responsive.media-breakpoint-down(xl) {
    gap: 18px;
  }
  @include responsive.media-breakpoint-down(elg) {
    padding-bottom: 0;
    gap: 7.33px;
  }

  &_logo {
    display: flex;
    gap: 12px;
    margin-left: -4px;
    @include responsive.media-breakpoint-down(elg) {
      display: none;
    }
  }

  &_submenu {
    display: flex;
    gap: 12px;
    @include responsive.media-breakpoint-down(elg) {
      gap: 7.33px;
    }
  }
}

.header-bottom {

    @media (max-width: 1024px) {
        margin-top: 32px !important;
    }

    @media (max-width: 576px) {
        margin-top: 24px !important;
    }

    overflow: auto;
    -ms-overflow-style: none;
    scrollbar-width: none;
    -webkit-overflow-scrolling: touch;

    &::-webkit-scrollbar {
        display: none;
    }
}

.header-bottom_submenu_item {
    height: 36px;
    padding: 8px;
    display: flex;
    align-items: center;
    gap: 4px;
    border-radius: 10px;
    background: var(--green-100);
    transition: background-color 0.2s ease-in-out;

    &.active {
        .header-bottom_submenu_item__arrow {
            transform: rotate(180deg);
        }
    }

    @media (any-hover: hover) {
        &:hover {
            background-color: #1A8362;
        }
    }

    @media (min-width: 577px) {
        height: 40px;
        padding: 10px 12px;
    }

    &__text {
        display: flex;
        align-items: center;
        color: var(--white-100);
        font-size: 14px;
        font-style: normal;
        font-weight: 600;
        line-height: 140%; /* 19.6px */
        white-space: nowrap;

        &:before {
            width: 20px;
            height: 20px;
            margin-right: 4px;
        }
    }

    &__arrow {
        width: 16px;
        height: 16px;
        transform: rotate(0deg);
        transition: transform 0.2s ease-in-out;
    }
}

.header-bottom_alphabet-btn {
    height: 36px;
    padding: 8px;
    width: fit-content;
    margin-left: auto;
    border-radius: 10px;
    background: var(--green-10);

    padding: 10px 16px;

    color: var(--green-100);
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 140%; /* 19.6px */
    white-space: nowrap;

    @media (min-width: 577px) {
        height: 40px;
        padding: 10px 16px;
    }
}
